  color: #2F58F7 !important;
  border: solid 2px #2F58F7;
  padding: 1rem 0.5rem;
  width: 180px;
  margin: 1rem 0.5rem;
  position: relative;
  font-size: 1.2rem;
  border-radius: 0;
  z-index: 1;
  transition: all 0.15s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.btn::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 0px;
  width: 100%;
  background-color: #2F58F7;
  z-index: -1;
  transition: all 0.15s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.btn:hover {
