.menu-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: visible;
  margin: 0;
  padding: 5px;
  cursor: pointer;
  transition-timing-function: linear;
  transition-duration: 0.15s;
  transition-property: opacity, filter;
  border: 0;
  background-color: transparent;
  z-index: 999;
}
.menu-toggle.active .inner {
  transform: rotateX(180deg) rotateY(180deg);
  background-color: transparent !important;
  background: #268CF5;
}
.menu-toggle.active .inner::before {
  transform: translate3d(0, 10px, 0) rotate(45deg);
  background: #268CF5;
}
.menu-toggle.active .inner::after {
  transform: translate3d(0, -10px, 0) rotate(-45deg);
  background: #268CF5;
}
.menu-toggle .box {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}
.menu-toggle .box .inner {
  top: 50%;
  display: block;
  margin-top: -2px;
  position: absolute;
  width: 40px;
  height: 2px;
  background: #268CF5;
  transition: transform 0.15s cubic-bezier(0.645, 0.045, 0.355, 1), background-color 0s cubic-bezier(0.645, 0.045, 0.355, 1) 0.1s;
}
.menu-toggle .box .inner::before, .menu-toggle .box .inner::after {
  content: "";
  display: block;
  position: absolute;
  width: 40px;
  height: 2px;
  background: #268CF5;
}
.menu-toggle .box .inner::before {
  top: -10px;
}
.menu-toggle .box .inner::after {
  bottom: -10px;
}

